+font("HelveticaNeue-Thin", HelveticaNeueCyr-Thin)
+font("HelveticaNeue-Light", HelveticaNeueLight)
+font("HelveticaNeue", HelveticaNeueCyr-Roman)
+font("HelveticaNeue-Bold", HelveticaNeueCyr-Bold)

=h100
  font-family: "HelveticaNeue-Thin", Arial, sans-serif
=h300
  font-family: "HelveticaNeue-Light", Arial, sans-serif
=h400
  font-family: "HelveticaNeue", Arial, sans-serif
=h700
  font-family: "HelveticaNeue-Bold", Arial, sans-serif

// extend
.in
  margin: 0 auto
  padding: 30px 28px 40px
  width: 869px
  +r(920)
    padding: 20px 15px 30px
    width: auto
    max-width: 500px
%in
  @extend .in

// variables
$yellow: #fffc00
$green: #41e262
$blue: #99deef
$d_blue: #1e2e5d
$d_red: #fc1147
$l_red: #fed6d6
$turquoise: #00e0bf
$gray: #d2d2d2

$c_text: #383838
$c_d-text: #424242
$c_logo: #00e0b3
$c_input: #353535
$c_blue: #00c2fd
$c_darc: #575757
$c_blue: #59d4ff

$bg_f: #335fa2
$bg_tw: #24a7df
$bg_in: #006b9c
$bg_g: #de4d3a

$bd_input: #e2e2e2
$bd_green: #b1ffd6
$bd-yellow: #eede7c
$bd-bord-blue: #00ceff


//reset
a
  text-decoration: none

h1, h2, h3, h4, h5, h6, em, b, strong, i, th, td
  font-weight: normal
  font-style: normal

body
  min-width: 480px
  color: $c_text
  +h300
  font-size: 14px
  line-height: 1.3

// header
.header
  padding: 28px 0 0
  text-align: center

// logo
.logo
  color: $c_d-text
  font-size: 12px
  h1
    margin-bottom: 8px
    color: $c_logo
    font-size: 38px
    line-height: 1
    +h100

// nav
.nav
  margin-top: 20px
  font-size: 0
  +h700
  background-color: $turquoise
  background-image: linear-gradient(18deg, $turquoise 0%, $green 100%)
  a
    position: relative
    padding: 0 3px
    margin-left: 45px
    height: 69px
    font-size: 14px
    line-height: 69px
    display: inline-block
    &:before
      height: 3px
    &:first-child
      margin-left: 0

// top
.top
  &.is-receive
    .step
      padding-top: 78px
      +r(920)
        padding-top: 42px
      &__in
        vertical-align: top

// step
.step
  height: 642px
  +r(920)
    padding: 42px 15px 45px
  &.is-mod
    padding: 20px 0 60px
    height: auto
    +r(920)
      padding: 52px 15px 45px
      height: auto
  &__in
    margin: 0 auto
    max-width: 920px
    vertical-align: middle
    font-size: 14px
    display: inline-block
  &__block
    padding: 30px 39px 24px 32px
    border-radius: 20px
    border: 1px solid $blue
    background: #fff
    &.is-mod
      border-radius: 20px 20px 0 0
      &:before
        background: url(../img/bg-bl.png) repeat-x 0 0
  &__shad
    position: absolute
    top: 0
    right: 100%
    bottom: -20px
    margin-right: -20px
    width: 60px
    opacity: .3
    z-index: -1
    background-image: linear-gradient(268deg, #b6b6b6 0%, transparent 100%)
    &:before,
    &:after
      position: absolute
      content: ''
      left: 0
    &:before
      top: 0
      +arr(60,20,#fff,tl)
    &:after
      bottom: 0
      +arr(60,20,#fff,br)
  &__btn
    margin-top: 50px
  &__but
    margin-top: 34px
  &__form
    .input
      min-width: 342px
  &__check
    margin-top: 15px
  &__row
    margin-top: 35px
    min-width: 342px
    +h400
    +clr
    &.is-mod
      padding-left: 20px
      padding-right: 20px
    &:first-child
      margin-top: 0
    .step__bold
      margin-bottom: 5px
    strong
      float: right
      margin-left: 2px
      +h700
    p
      & + .step__bold,
      & + p
      margin: 5px 0 0 0
  &__bold
    +h700
  &__wrap
    width: 342px
    .price
      margin-top: 15px
    .title-middle
      margin-bottom: 18px
  &__text
    margin: 20px 0 10px
  &__wrapper
    margin: 0 auto
    width: 315px
    .step__row
      position: relative
      margin: 18px 0 0
      padding: 18px 0 0
      min-width: 242px
      width: 242px
      &.is-mod
        padding-left: 20px
        padding-right: 20px
      &:first-child
        margin-top: 0
        padding-top: 0
        &:before
          display: none
      &:nth-child(even)
        &:before
          width: 84px
      &:before
        position: absolute
        content: ''
        top: 0
        left: 50%
        transform: translate(-50%,0)
        width: 184px
        height: 1px
        background: #ebebeb
    .step__btn
      margin-top: 35px
      & + .step__btn
        margin-top: 20px
    .title-middle
      margin-bottom: 18px
    .price
      +h700
      font-size: 16px
  &__sms
    width: 355px
    .input-field
      width: 100%
    .btn
      margin-top: 35px
      & + .btn
        margin-top: 20px
    .title-middle
      margin-bottom: 18px
    .sms-code
      margin-top: 18px
  &__nav
    margin: 20px 0 27px
    font-size: 14px
    +h700
    a
      color: #00d0ef
      &:hover
        border-color: #00d0ef
  &__numb
    position: relative
  &__inform
    margin: 0 -65px 25px
    max-width: 520px
    font-size: 16px
    +h400
    strong
      +h700
  .commission
    font-size: 12px

// title
.title
  margin-bottom: 18px
  text-align: center
  font-size: 22px
  +h700
  color: $c_darc

// title-small
.title-small
  font-size: 14px
  +h700

// title-middle
.title-middle
  margin-bottom: 30px
  color: $c_d-text
  font-size: 16px
  +h700

// price
.price
  font-size: 16px
  strong
    +h700

// btn
.btn
  position: relative
  height: 49px
  font-size: 16px
  min-width: 247px
  +h700
  color: #fff
  line-height: 49px
  text-align: center
  text-shadow: 0 1px 0 rgba(160, 160, 160, 0.43)
  border-radius: 49px
  overflow: hidden
  transition: background-color .2s
  background-color: $turquoise
  display: inline-block
  &:hover
    &:before
      left: -100%
  &:active
    background-color: darken($turquoise, 10%)
  &:before
    position: absolute
    content: ''
    left: 0
    top: 0
    width: 100%
    height: 100%
    transition: left .3s
    border-radius: 49px
    background-image: linear-gradient(18deg, $turquoise 0%, $green 100%)
  span
    text-transform: lowercase
    position: relative
    z-index: 1
  &_bord
    color: $c_darc
    font-size: 13px
    height: 43px
    line-height: 43px
    border-radius: 43px
    &:hover
      span
        color: #fff
        &:before
          left: -100%
    span
      position: absolute
      top: 1px
      left: 1px
      right: 1px
      bottom: 1px
      border-radius: 47px
      transition: color .2s
      overflow: hidden
      display: inline-block
      &:before
        position: absolute
        content: ''
        left: 0
        top: 0
        width: 100%
        height: 100%
        background: #fff
        transition: left .3s
        z-index: -1
        border-radius: 47px

// input
.input
  line-height: 1.2
  +h400
  &:focus
    border-color: $blue

// checkbox
.checkbox
  position: relative
  font-size: 14px
  display: block
  position: relative
  display: inline-block
  vertical-align: top
  cursor: pointer
  user-select: none
  input
    position: absolute
    visibility: hidden
    opacity: 0
    top: 0
    left: 0
    &:checked + span
      &:before
        opacity: 0
        transform: scale(0.1)
      i
        top: 10px
        visibility: visible
        transform: scale(1)
  span
    font-size: 15px
    line-height: 1
    padding: 7px 0 6px 35px
    display: inline-block
    &:before
      content: ""
      position: absolute
      top: 0
      left: 0
      border: 1px solid $blue
      border-radius: 50%
      width: 28px
      height: 28px
      transition: opacity .2s, transform .2s
    a
      +h400
      color: $c_blue
      transition: border-color .2s
      border-bottom: 1px solid transparent
      &:hover
        border-color: $c_blue
    i
      font-size: 11px
      color: $c_blue
      position: absolute
      top: 5px
      left: 7px
      transform: scale(0)
      transition: transform .2s, top .2s

// line
.line
  padding: 0
  background-color: $turquoise
  background-image: linear-gradient(18deg, $turquoise 0%, $green 100%)

// rates
.rates
  text-align: center
  +r(920)
    padding: 0 40px
  &__in
    @extend %in
    border: 1px solid $bd_green
    +r(920)
      max-width: 100%
  .btn
    margin-top: 22px
  p
    line-height: 1.9
  strong
    +h700

// answer
.answer
  &__in
    @extend %in
  &__text
    padding-right: 22px

// annex
.annex
  text-align: center
  &__in
    margin-bottom: 32px
    @extend %in
    padding: 0
    +r(920)
      max-width: 100%
  &__row
    margin-top: 25px
    font-size: 0
  .app
    margin-left: 25px
    &:first-child
      margin-left: 0

// question
.question
  &__in
    @extend %in
    padding-bottom: 20px
  .title
    margin-bottom: 5px

// cards
.cards
  margin: 10px 0 30px
  width: 872px
  +r(920)
    margin: 0 0 50px
    padding: 0 15px
    width: auto
  &__col
    +r(920)
      margin: 50px 0 0

// card
.card
  position: relative
  border-radius: 20px
  border: 1px solid $blue
  background: #fff
  &__shad
    position: absolute
    top: 0
    right: 100%
    bottom: -20px
    margin-right: -20px
    width: 42px
    opacity: .3
    z-index: -1
    background-image: linear-gradient(268deg, #b6b6b6 0%, transparent 100%)
    &:before,
    &:after
      position: absolute
      content: ''
      left: 0
    &:before
      top: 0
      +arr(42,30,#fff,tl)
    &:after
      bottom: 0
      +arr(42,20,#fff,br)
  &__in
    padding: 38px 39px 37px 32px
  &__icon
    color: $c_blue
    +h400
  &__label
    top: 3px
    color: #bdbbbb
    +h400
  &__question
    color: #3276fb
    +h400
    &:hover
      border-color: #3276fb
  &__cont
    padding: 39px 39px 37px 32px
  &__ul
    width: 418px
    +r(920)
      width: 100%
    margin-right: -1px
    overflow: hidden
    border-radius: 0 0 20px 20px
    background: #9de0f1
    li
      position: relative
      border-top: 1px solid $blue
      background: #9de0f1
      &:hover,
      &.is-active
        background: #c0eaf6
      &:before
        position: absolute
        content: ''
        top: -1px
        left: 0
        bottom: -1px
        width: 1px
        background: #fff
      &:first-child
        &:before
          display: none
        a
          padding-left: 47px
        .icon
          left: 20px
    a
      padding: 15px 35px 10px 41px
      height: 64px
      color: $c_text
      +h400
      .icon
        top: 18px
        left: 23px
// error-xext
.error-xext
  color: #fc034a

// input-field
.input-field
  margin: 0 auto 12px
  width: 342px
  +h400
  &__shad
    position: absolute
    top: -18px
    right: 100%
    bottom: -8px
    width: 16px
    opacity: .2
    z-index: -1
    background-image: linear-gradient(268deg, #b6b6b6 0%, transparent 100%)
    &:before,
    &:after
      position: absolute
      content: ''
      left: 0
    &:before
      top: 0
      +arr(42,30,#fff,tl)
    &:after
      bottom: 0
      +arr(42,20,#fff,br)

// accord
.accord
  border-top: 1px solid $gray
  &:first-child
    border-top: none
  &__top
    +h700
  &__block
    +h400

// footer
.footer
  &__in
    margin: 0 auto
    max-width: 920px
    padding: 32px 15px 50px
  &__col
    margin-left: 35px
    +h400
  &__center
    margin-top: 28px
  .day
    line-height: 1
  strong
    +h700

// partners
.partners
  margin-top: 30px
